nav {
    display: flex;
    position: fixed;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    background: rgb(31, 15, 48);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0);
}
.logo-img {

    height: 90px;
    float: left;
    margin-right: 20%;
}
.menu-link ul {
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.menu-link li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

.menu-link li a {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: antiquewhite;
    transition: 0.3s ease-in-out;
}
.menu-link li a:hover,
.menu-link li a.active {
    color:rgb(52, 157, 152);
}
.menu-link li a:hover::after,
.menu-link li a.active::after{
    content: "";
    width: 30%;
    height: 2px;
    background: rgb(52, 157, 152);
    position: absolute;
    bottom: -4px;
    left: 20px;
}
    
.social-media-link {
    display: flex;
}
.social-media-link li {
    list-style: none;
    padding: 0 20px;
    position: relative;

}
.social-media-link li a:hover {
    color: bisque;
}
